import React from 'react'
import Typography from '@material-ui/core/Typography'
import Button, { ButtonProps } from '@material-ui/core/Button'
import Tooltip from '@material-ui/core/Tooltip'
import CircularProgress from '@material-ui/core/CircularProgress'
import CloudDownloadIcon from '@material-ui/icons/CloudDownload'
import moment from 'moment'
import { Project } from '@riverscapes/common'
import { makeStyles } from '@material-ui/core/styles'

const useStyles = makeStyles((theme) => ({
    root: {
        marginLeft: theme.spacing(1)
    },
    disabledButton: {
        opacity: 0.5
    }
}))

interface Props {
    title?: string
    project: Project
    pendingSince?: number
    downloadZipFile: (proj: Project) => Promise<void>
    btnProps?: ButtonProps
}

const DownloadZipBtn: React.FC<Props> = ({ title, project, pendingSince, btnProps, downloadZipFile }: Props) => {
    const classes = useStyles()

    let btnRender = (
        <Tooltip
            title={
                <React.Fragment>
                    <Typography variant="body1">Download Project Zip</Typography>
                    <Typography variant="caption" paragraph>
                        Click to download this project as a single zip file
                    </Typography>
                    <Typography variant="caption" paragraph>
                        There may be a delay if the zip file needs to be built.
                    </Typography>
                </React.Fragment>
            }
        >
            <Button
                variant="outlined"
                color="primary"
                onClick={(e: React.MouseEvent<HTMLButtonElement, MouseEvent>): void => {
                    e.preventDefault()
                    e.stopPropagation()
                    downloadZipFile(project)
                }}
                startIcon={<CloudDownloadIcon />}
                {...btnProps}
            >
                {title || 'ZIP'}
            </Button>
        </Tooltip>
    )
    if (pendingSince) {
        btnRender = (
            <Tooltip
                title={
                    <React.Fragment>
                        <Typography variant="body1">Preparing Zip</Typography>
                        <Typography variant="caption" paragraph>
                            The warehouse is building your zip file for you.
                        </Typography>
                        <Typography variant="caption" paragraph>
                            started {moment(pendingSince, 'x').fromNow()}
                        </Typography>
                    </React.Fragment>
                }
            >
                <Button
                    variant="outlined"
                    disableFocusRipple
                    disableRipple
                    disableTouchRipple
                    color="primary"
                    onClick={(e: React.MouseEvent<HTMLButtonElement, MouseEvent>): void => {
                        e.preventDefault()
                        e.stopPropagation()
                    }}
                    className={classes.disabledButton}
                    startIcon={<CircularProgress color="secondary" size={24} thickness={3} />}
                    {...btnProps}
                >
                    {title || 'ZIP'}
                </Button>
            </Tooltip>
        )
    }

    return <div className={classes.root}>{btnRender}</div>
}

export default DownloadZipBtn
