import React from 'react'
import { WarehouseRoleEnum, UserType, Program } from '@riverscapes/common'
import { Loading } from '@northarrowresearch/react-common'
import Paper from '@material-ui/core/Paper'
import { makeStyles } from '@material-ui/core/styles'
import { Typography } from '@material-ui/core'
import { warehouseIconUrl } from '../../config'
import AdminList from './AdminList'

const useStyles = makeStyles((theme) => ({
    rootContainer: {
        height: '100%',
        width: '100%',
        overflow: 'hidden',
        position: 'relative'
    },
    root: {
        height: '100%',
        width: '100%',
        display: 'flex',
        overflow: 'hidden',
        flexDirection: 'column'
    },
    paperContainer: {
        display: 'flex',
        width: '100%',
        height: '100%',
        overflow: 'hidden',
        '&>:first-child': {
            flex: '1 1 60%'
        },
        '&>:last-child': {
            flex: '1 1 40%'
        }
    },
    instructions: {
        color: theme.palette.primary.contrastText,
        background: theme.palette.primary.main,
        margin: theme.spacing(2),
        padding: theme.spacing(2)
    },
    list: {
        flexGrow: 1,
        overflow: 'scroll',
        margin: theme.spacing(1, 2)
    },
    loading: {
        // border: '10px solid red',
        background: '#FFFFFFCC',
        position: 'absolute',
        zIndex: 1000
    }
}))

interface Props {
    users: UserType[]
    program: Program
    isSuperUser?: boolean
    loading?: boolean
    onChange: (userId: string, newRole: WarehouseRoleEnum | null) => void
}

function sortUsers(ulist: UserType[]): void {
    ulist.sort((a: UserType, b: UserType): number => {
        return a.name.toLowerCase().localeCompare(b.name.toLowerCase())
    })
}

const AdminPanel: React.FC<Props> = ({ users, program, loading, isSuperUser, onChange }: Props) => {
    const classes = useStyles()
    const progUsers = users.filter((u) => u.role)
    sortUsers(progUsers)
    const nonUsers = users.filter((u) => !u.role)
    sortUsers(nonUsers)

    const isProgPublic = program && program.meta && program.meta.access && program.meta.access === 'PUBLIC'
    const logoSVG = <img src={warehouseIconUrl} alt="logo" />
    return (
        <div className={classes.rootContainer}>
            {loading && (
                <Loading
                    logoSVG={logoSVG}
                    title={'Riverscapes Warehouse'}
                    message={`Loading Users: ${users.length}`}
                    containerProps={{ maxWidth: 'xl', className: classes.loading }}
                />
            )}
            <div className={classes.root}>
                <Paper className={classes.instructions}>
                    <Typography variant="h4" color="inherit">
                        User admin: {program && program.name}
                    </Typography>
                    {isProgPublic ? (
                        <Typography variant="body2" color="inherit">
                            This program is <strong>public</strong>. All authenticated users in the Riverscapes user
                            pool will be able to see and download projects.
                            <strong>Participants</strong> can add or remove projects.
                        </Typography>
                    ) : (
                        <Typography variant="body2" color="inherit">
                            This program is <strong>private</strong> so you have to explicitly add users to view it.{' '}
                            <strong>Participants</strong> can add or remove projects.
                        </Typography>
                    )}
                </Paper>
                <div className={classes.paperContainer}>
                    <AdminList
                        users={progUsers}
                        onChange={onChange}
                        isSuperUser={isSuperUser}
                        title="Current program users:"
                        description="Everyone in this list is an Observer and can view and download all projects."
                    />
                    <AdminList
                        users={nonUsers}
                        onChange={onChange}
                        isSuperUser={isSuperUser}
                        title="Add users:"
                        description={
                            isProgPublic
                                ? 'Users in this list CAN see everything in the program because the program is PUBLIC'
                                : 'Users in this list cannot see or donwload projects in this program'
                        }
                    />
                </div>
            </div>
        </div>
    )
}
export default AdminPanel
