import React from 'react'
import Typography from '@material-ui/core/Typography'
import Button from '@material-ui/core/Button'
import Paper from '@material-ui/core/Paper'
import LockOpenIcon from '@material-ui/icons/LockOpen'
import { makeStyles } from '@material-ui/core/styles'

const useStyles = makeStyles((theme) => ({
    rootContainer: {
        height: '100%',
        display: 'flex',
        flexDirection: 'column'
    },
    spacer: {
        flexGrow: 1
    },
    root: {
        padding: theme.spacing(2, 4, 4, 4)
    },
    logoContainer: {
        padding: theme.spacing(2),
        textAlign: 'center'
    },
    logo: {}
}))

interface Props {
    signIn: () => Promise<void>
}

const LoginScreen: React.FC<Props> = ({ signIn }: Props) => {
    const classes = useStyles()

    return (
        <div className={classes.rootContainer}>
            <div className={classes.spacer} />
            <Paper className={classes.root}>
                <div className={classes.logoContainer}>
                    <img className={classes.logo} src={process.env.PUBLIC_URL + '/RiverscapesLogo.png'} alt="logo" />
                </div>
                <Typography paragraph variant="body1">
                    Welcome to the Riverscapes Warehouse. Please click the button below to log in.
                </Typography>
                <Typography paragraph variant="body1">
                    If you don't have an account you will be prompted to use your Google account or create a new account
                    using another email address.
                </Typography>
                <Button
                    variant="contained"
                    onClick={signIn}
                    color="primary"
                    fullWidth
                    size="large"
                    startIcon={<LockOpenIcon />}
                >
                    Sign In
                </Button>
            </Paper>
            <div className={classes.spacer} />
        </div>
    )
}
export default LoginScreen
