import React, { useState, useEffect, useRef } from 'react'
import { search } from '@riverscapes/common'
import log from 'loglevel'
import { Program } from '@riverscapes/common'
import SearchForm from '../components/Search/SearchForm'
import { ProjTotals, setSearchParamsType, SearchParams } from '../types'

interface Props {
    program?: Program
    getCurrentToken: () => Promise<string>
    url: string
    searchParams: SearchParams
    setSearchParams: (params: SearchParams) => void
}

const SearchFormConnect: React.FC<Props> = ({
    getCurrentToken,
    url,
    program,
    searchParams,
    setSearchParams
}: Props) => {
    const [projTotals, setProjTotals] = useState<ProjTotals>({})
    const projTotalsRef = useRef<{}>()
    projTotalsRef.current = projTotals

    useEffect(() => {
        if (program && url) {
            getCurrentToken()
                // The initial search is just to get at the total value
                .then((jwt) => search(url, jwt, program.machineName, { limit: 0 }))
                .then(
                    ({ data }) =>
                        data &&
                        setProjTotals({
                            ...projTotalsRef.current,
                            __all: data.search ? data.search.total : 0
                        })
                )
                .catch((err) => {
                    log.error(err)
                })
            // HEre's where we get the product totals
            program.products.reduce((promiseChain, prod) => {
                const currTask = (): Promise<void> =>
                    getCurrentToken()
                        .then((jwt) => search(url, jwt, program.machineName, { projType: prod.id, limit: 0 }))
                        .then(({ data }) => {
                            data &&
                                setProjTotals({
                                    ...projTotalsRef.current,
                                    [prod.id]: data.search ? data.search.total : 0
                                })
                        })
                        .catch((err) => {
                            setProjTotals({ ...projTotalsRef.current, [prod.id]: 0 })
                            log.error(err)
                        })
                return promiseChain.then(currTask)
            }, Promise.resolve())
        }
    }, [program, url, getCurrentToken])

    return (
        <SearchForm
            projTotals={projTotals}
            program={program as Program}
            searchParams={searchParams}
            setSearchParams={setSearchParams as setSearchParamsType}
        />
    )
}
SearchFormConnect['whyDidYouRender'] = true
export default SearchFormConnect
