import { useEffect, useRef, useState } from 'react'
import { Match } from 'react-router-dom'
import { Program } from '@riverscapes/common'
import { mandatoryMetaColors } from '../config'
import log from 'loglevel'

const useProgram = (
    match?: Match,
    programs?: Program[],
    onProgramChange?: (newProgram: Program) => void
): Program | undefined => {
    const [program, setProgram] = useState<Program>()
    const currProgramRef = useRef<Program>()
    currProgramRef.current = program

    useEffect((): void => {
        if (!match || !programs || !match.params || !match.params.program) return
        try {
            const newProg = programs?.find((p) => p.machineName === match.params.program)
            if (newProg && (!currProgramRef.current || currProgramRef.current.machineName !== newProg.machineName)) {
                // Add colours in for the mandatory metadata
                newProg.collections = newProg.collections.map((c, idx) => ({
                    ...c,
                    color: mandatoryMetaColors[idx % mandatoryMetaColors.length]
                }))
                currProgramRef.current = newProg
                log.debug('useProgram', { match, newProg, programs })
                setProgram(newProg)
                if (onProgramChange) onProgramChange(newProg)
            }
        } catch {
            return
        }
    }, [match, programs, onProgramChange])

    return program
}

export default useProgram
