import React from 'react'
import { makeStyles } from '@material-ui/core/styles'
import IconButton from '@material-ui/core/IconButton'
import Typography from '@material-ui/core/Typography'
import Tooltip from '@material-ui/core/Tooltip'

import CloudDownloadIcon from '@material-ui/icons/CloudDownload'
import TreeItem from '@material-ui/lab/TreeItem'

import filesize from 'filesize'

import { Project, FileTreeObj } from '@riverscapes/common'
import style from './RecurseTreeItem.style'

const useStyles = makeStyles(style)

interface Props {
    item: FileTreeObj
    fileDict: {}
    project: Project
    fileClick: (project: Project, path: string) => void
    keyStr: string
}

const RecurseTreeItem: React.FC<Props> = ({ item, fileDict, keyStr, project, fileClick }: Props) => {
    const classes = useStyles()
    if (item.children) {
        const childNodes = item.children.map((c, idx) => (
            <RecurseTreeItem
                keyStr={`${keyStr}-l${idx}`}
                key={`${keyStr}-l${idx}`}
                item={c}
                fileDict={fileDict}
                project={project}
                fileClick={fileClick}
            />
        ))
        return (
            <TreeItem
                nodeId={keyStr}
                className={classes.treeFolder}
                classes={{
                    content: classes.treeFolderContent
                }}
                label={<div className={classes.treeFolderText}>{item.name}</div>}
            >
                {childNodes}
            </TreeItem>
        )
    } else {
        const lookup = item.path ? fileDict[item.path] : null
        return (
            <TreeItem
                nodeId={keyStr}
                className={classes.treeFile}
                classes={{
                    root: classes.treeItemRoot,
                    content: classes.treeFileContent
                }}
                label={
                    <div className={classes.treeItemContainer}>
                        <div className={classes.treeItemText}>
                            <Typography color="textPrimary" component="div" variant="body1">
                                {item.name}
                            </Typography>
                            <Typography color="textSecondary" component="div" variant="caption">
                                {filesize(parseInt(lookup.size, 10))} --{' '}
                                <span className={classes.allowSelect}>{item.path}</span>
                            </Typography>
                        </div>
                        <Tooltip
                            title={
                                <React.Fragment>
                                    <Typography variant="body1">Download: {item.path}</Typography>
                                </React.Fragment>
                            }
                        >
                            <IconButton
                                disableRipple
                                className={classes.treeItemIcon}
                                color="primary"
                                onClick={(): void => fileClick(project, item.path)}
                            >
                                <CloudDownloadIcon />
                            </IconButton>
                        </Tooltip>
                    </div>
                }
                endIcon={lookup.icon}
            />
        )
    }
}

export default RecurseTreeItem
