import React from 'react'
import IconButton from '@material-ui/core/IconButton'

import PersonAddIcon from '@material-ui/icons/PersonAdd'
import FaceIcon from '@material-ui/icons/Face'
import CancelIcon from '@material-ui/icons/Cancel'

import FormControlLabel from '@material-ui/core/FormControlLabel'
import ListItem from '@material-ui/core/ListItem'
import Tooltip from '@material-ui/core/Tooltip'
import Avatar from '@material-ui/core/Avatar'
import ListItemText from '@material-ui/core/ListItemText'
import ListItemAvatar from '@material-ui/core/ListItemAvatar'
import ListItemSecondaryAction from '@material-ui/core/ListItemSecondaryAction'
import Switch from '@material-ui/core/Switch'

// import { FieldDef } from '../types'
// import MenuIcon from '@material-ui/icons/Menu'
import { WarehouseRoleEnum, UserType } from '@riverscapes/common'
import { makeStyles, Theme, StyleRules } from '@material-ui/core/styles'
// import log from 'loglevel'

export interface UserRowType extends UserType {
    loading?: boolean
}

interface Props {
    user: UserRowType
    isSuperUser?: boolean
    onChange: (userId: string, newRole: WarehouseRoleEnum | null) => void
}

const useStyles = makeStyles(
    (theme: Theme): StyleRules => ({
        root: {},
        allowSelect: {
            userSelect: 'text',
            cursor: 'auto'
        },
        google: {
            ...theme.typography.caption,
            borderRadius: theme.spacing(1.5),
            padding: theme.spacing(0.5, 1),
            background: theme.palette.primary.main,
            color: theme.palette.primary.contrastText,
            marginLeft: theme.spacing(2),
            border: `1px solid ${theme.palette.primary.dark}`
        },
        removeIcon: {
            marginLeft: theme.spacing(2)
        }
    })
)

const AdminRow: React.FC<Props> = ({ user, onChange, isSuperUser }: Props) => {
    const classes = useStyles()
    const loading = user.loading

    const onSwitchChange = (swRole: WarehouseRoleEnum, swVal: boolean): void => {
        if (swRole === WarehouseRoleEnum.Admin) {
            if (swVal) onChange(user.username, WarehouseRoleEnum.Admin)
            else onChange(user.username, WarehouseRoleEnum.Participant)
        } else if (swRole === WarehouseRoleEnum.Participant) {
            if (swVal) onChange(user.username, WarehouseRoleEnum.Participant)
            else onChange(user.username, WarehouseRoleEnum.Observer)
        }
    }
    const superUser = user.role === WarehouseRoleEnum.SuperUser

    return (
        <ListItem disabled={loading || superUser} className={classes.root} divider>
            <ListItemAvatar>
                {user.picture && user.picture.length > 0 ? (
                    <Avatar alt={user.name} src={user.picture} />
                ) : (
                    <Avatar alt={user.name}>
                        <FaceIcon color="inherit" />
                    </Avatar>
                )}
            </ListItemAvatar>
            <ListItemText
                id="switch-list-label-wifi"
                primary={
                    <span>
                        <span className={classes.allowSelect}>{user.name}</span>
                        {user.username.startsWith('google') && <span className={classes.google}>Google</span>}
                    </span>
                }
                secondary={<span className={classes.allowSelect}>{user.email}</span>}
            />
            {user.role && (
                <ListItemSecondaryAction>
                    {isSuperUser && !superUser && (
                        <FormControlLabel
                            control={
                                <Switch
                                    edge="end"
                                    onChange={(e): void => onSwitchChange(WarehouseRoleEnum.Admin, e.target.checked)}
                                    checked={
                                        [WarehouseRoleEnum.Admin, WarehouseRoleEnum.SuperUser].indexOf(
                                            user.role as WarehouseRoleEnum
                                        ) > -1
                                    }
                                    disabled={
                                        loading ||
                                        [WarehouseRoleEnum.Observer].indexOf(user.role as WarehouseRoleEnum) > -1
                                    }
                                    inputProps={{ 'aria-labelledby': 'switch-list-label-wifi' }}
                                />
                            }
                            labelPlacement="start"
                            label="Program Admin"
                        />
                    )}
                    {!superUser && (
                        <FormControlLabel
                            control={
                                <Switch
                                    edge="end"
                                    onChange={(e): void =>
                                        onSwitchChange(WarehouseRoleEnum.Participant, e.target.checked)
                                    }
                                    checked={
                                        [
                                            WarehouseRoleEnum.Participant,
                                            WarehouseRoleEnum.Admin,
                                            WarehouseRoleEnum.SuperUser
                                        ].indexOf(user.role as WarehouseRoleEnum) > -1
                                    }
                                    disabled={
                                        loading ||
                                        [WarehouseRoleEnum.Admin, WarehouseRoleEnum.SuperUser].indexOf(
                                            user.role as WarehouseRoleEnum
                                        ) > -1
                                    }
                                    inputProps={{ 'aria-labelledby': 'switch-list-label-wifi' }}
                                />
                            }
                            labelPlacement="start"
                            label="Participant"
                        />
                    )}

                    {!superUser && (
                        <Tooltip title={'Remove this user from the program'}>
                            <>
                                {/* Tooltip cannot have a disabled child */}
                                <IconButton
                                    edge="end"
                                    className={classes.removeIcon}
                                    aria-label="delete"
                                    color="secondary"
                                    disabled={loading}
                                    onClick={(): void => onChange(user.username, null)}
                                >
                                    <CancelIcon />
                                </IconButton>
                            </>
                        </Tooltip>
                    )}
                </ListItemSecondaryAction>
            )}
            {!user.role && (
                <ListItemSecondaryAction>
                    <Tooltip title={'Add this user to the program'}>
                        <>
                            {/* Tooltip cannot have a disabled child */}
                            <IconButton
                                edge="end"
                                aria-label="delete"
                                color="secondary"
                                disabled={loading}
                                onClick={(): void => onChange(user.username, WarehouseRoleEnum.Observer)}
                            >
                                <PersonAddIcon />
                            </IconButton>
                        </>
                    </Tooltip>
                </ListItemSecondaryAction>
            )}
        </ListItem>
    )
}

export default AdminRow
