import React from 'react'
import Paper from '@material-ui/core/Paper'
import Divider from '@material-ui/core/Divider'

import List from '@material-ui/core/List'
import SearchResult from './SearchResult'
import SearchResultLoading from './SearchResultLoading'

import { makeStyles } from '@material-ui/core/styles'

import { PaginationWidget } from '@northarrowresearch/react-common'
import { Project, Program, SearchResultsInterface } from '@riverscapes/common'
import { SearchParams, PendingZips } from '../../types'

const useStyles = makeStyles((theme) => ({
    root: {
        padding: theme.spacing(2),
        margin: theme.spacing(2)
    }
}))

interface Props {
    data?: SearchResultsInterface
    loading?: boolean
    program: Program
    searchParams: SearchParams
    setSearchParams: (params: SearchParams) => void
    pendingZips: PendingZips
    navigate: (progName: string, projId?: string) => void
    downloadZipFile: (proj: Project) => Promise<void>
}

const SearchResults: React.FC<Props> = ({
    data,
    program,
    loading,
    searchParams,
    setSearchParams,
    pendingZips,
    navigate,
    downloadZipFile
}: Props) => {
    const classes = useStyles()

    //  The loading state of this object is just a bunch of empty skeletons
    if (loading) {
        return (
            <Paper className={classes.root}>
                <PaginationWidget
                    data={{
                        total: 0,
                        startIndex: searchParams.nextToken,
                        pageSize: searchParams.limit
                    }}
                    loading={loading}
                    handlePaginationEvent={(newIndex: number): void =>
                        setSearchParams({
                            ...searchParams,
                            nextToken: newIndex
                        })
                    }
                />
                <Divider />
                <List>
                    {Array(searchParams.limit || 10)
                        .fill(null)
                        .map((x, idx) => (
                            <SearchResultLoading key={`result-${idx}`} />
                        ))}
                </List>
            </Paper>
        )
    }
    //  Maybe search returned nothing
    if (!data || !data.projects || data.projects.length === 0) {
        return (
            <Paper className={classes.root}>
                <div>No Projects Found</div>
            </Paper>
        )
    }

    return (
        <Paper className={classes.root}>
            <PaginationWidget
                data={{
                    total: data.total,
                    startIndex: searchParams.nextToken,
                    pageSize: searchParams.limit
                }}
                handlePaginationEvent={(newIndex: number): void =>
                    setSearchParams({
                        ...searchParams,
                        nextToken: newIndex
                    })
                }
            />
            <Divider />
            <List>
                {data &&
                    data.projects.map((r, idr) => {
                        const project = new Project(r)
                        return (
                            <SearchResult
                                key={`result-${idr}`}
                                odd={Boolean(idr % 2)}
                                project={project}
                                program={program}
                                pendingZips={pendingZips}
                                downloadZipFile={downloadZipFile}
                                onClick={(): void => navigate(program.machineName, project.id)}
                            />
                        )
                    })}
            </List>
        </Paper>
    )
}

export default SearchResults
