import React, { useState, useRef, useEffect } from 'react'
import TextField from '@material-ui/core/TextField'
import FormControl from '@material-ui/core/FormControl'
import Select from '@material-ui/core/Select'
import MenuItem from '@material-ui/core/MenuItem'
import InputLabel from '@material-ui/core/InputLabel'
import Button from '@material-ui/core/Button'
import SearchIcon from '@material-ui/icons/Search'
import { makeStyles, Paper } from '@material-ui/core'

import { appDefaults } from '../../config'

import SearchAdvancedRow from './SearchAdvancedRow'
import { ProjTotals, setSearchParamsType, SearchParams } from '../../types'
import { CollectionType, Program } from '@riverscapes/common'

const useStyles = makeStyles((theme) => ({
    root: {
        padding: theme.spacing(2),
        margin: theme.spacing(2)
    },
    spacer: {
        flexGrow: 1
    },
    actionBar: {
        display: 'flex',
        width: '100%',
        '&>*': {
            marginLeft: theme.spacing(1)
        }
    },
    advanced: {},
    iconButton: {},
    textField: {},
    projTypeLine: {
        display: 'flex'
    },
    projType: {
        marginRight: theme.spacing(1),
        flexGrow: 1,
        maxWidth: 300
    },
    resultsNum: {
        flexShrink: 0
    },
    projTypeMenuItem: {
        display: 'flex'
    },
    projTypeMenuItemSelected: {
        display: 'flex'
    },
    projTypeMenuItemName: {
        flexGrow: 1
    },
    projTypeMenuItemNum: {
        color: theme.palette.text.secondary,
        fontSize: '0.75rem'
    }
}))

interface Props {
    program: Program
    projTotals: ProjTotals
    searchParams?: SearchParams
    setSearchParams: setSearchParamsType
}

/**
 * This is the over-arching wrapper for the app. It combines all the
 * same place. Time will tell if that's a good strategy.
 */
const SearchForm: React.FC<Props> = ({ projTotals, program, searchParams, setSearchParams }: Props) => {
    const classes = useStyles()
    const [advancedVals, setAdvancedVals] = useState<string[][]>([])
    const searchParamsRef = useRef<SearchParams | undefined>(searchParams)
    const searchMetaInputRef = useRef<HTMLInputElement>()
    searchParamsRef.current = searchParams

    function submitSearch(params: SearchParams | null, advancedParams?: string[][]): void {
        const newParams: SearchParams = { ...(params || (searchParamsRef.current as SearchParams) || searchParams) }
        const newAdvanced = [...(advancedParams || advancedVals)]

        if (newParams.projType === '__all') delete newParams.projType

        const advFinal =
            newAdvanced &&
            newAdvanced.length > 0 &&
            newAdvanced.filter(([k, v]) => k && v && k.length > 0 && v.length > 0)

        const submitParams: SearchParams = { ...newParams }
        if (advFinal && advFinal.length > 0) submitParams.metaArr = JSON.stringify(advFinal)

        if (advancedParams) setAdvancedVals(advancedParams)
        setSearchParams(submitParams)
    }

    useEffect(() => {
        if (searchMetaInputRef.current) searchMetaInputRef.current.value = (searchParams as SearchParams).metaStr || ''
    }, [searchParams])

    const toggleAdvanced = (): void => {
        if (!searchParamsRef.current) return
        if (advancedVals.length > 0) {
            submitSearch(searchParamsRef.current, [])
        } else {
            submitSearch(searchParamsRef.current, [['', '']])
        }
    }

    const projectArr =
        program && program.products ? Object.values(program.products).map(({ name, id }) => ({ name, id })) : []

    const allAdvancedOptions: CollectionType[] = program ? program.collections : []

    allAdvancedOptions.sort((a, b) => {
        if (a.name.toLowerCase() < b.name.toLowerCase()) return -1
        if (a.name.toLowerCase() > b.name.toLowerCase()) return 1
        return 0
    })
    projectArr.sort((a, b) => {
        if (a.name.toLowerCase() < b.name.toLowerCase()) return -1
        if (a.name.toLowerCase() > b.name.toLowerCase()) return 1
        return 0
    })

    const handleKeyPress = (e): void => {
        if (e.keyCode === 13) {
            setSearchParams(searchParamsRef.current as SearchParams)
        }
    }
    return (
        <Paper className={classes.root}>
            {program && program.products && (
                <div className={classes.projTypeLine}>
                    <FormControl fullWidth className={classes.projType}>
                        <InputLabel htmlFor="projectType">Project Type</InputLabel>
                        <Select
                            id="projectType"
                            inputProps={{
                                className: classes.projTypeMenuItemSelected
                            }}
                            value={searchParamsRef.current?.projType || '__all'}
                            onChange={(event: React.ChangeEvent<{ name?: string; value: unknown }>): void => {
                                const projType =
                                    event.target.value !== '_all' ? (event.target.value as string) : undefined
                                submitSearch({ ...(searchParamsRef.current as SearchParams), projType })
                            }}
                        >
                            <MenuItem className={classes.projTypeMenuItem} value="__all">
                                <div className={classes.projTypeMenuItemName}>All Project Types</div>
                                <div className={classes.projTypeMenuItemNum}>
                                    {projTotals.__all > -1 ? `${projTotals.__all}` : '...'}
                                </div>
                            </MenuItem>
                            {projectArr.map(({ name: prodName, id: prodId }, idx) => (
                                <MenuItem className={classes.projTypeMenuItem} key={`prod-${idx}`} value={prodId}>
                                    <div className={classes.projTypeMenuItemName}>{prodName}</div>
                                    <div className={classes.projTypeMenuItemNum}>
                                        {projTotals[prodId] > -1 ? `${projTotals[prodId]}` : '...'}
                                    </div>
                                </MenuItem>
                            ))}
                        </Select>
                    </FormControl>
                    <div className={classes.spacer} />
                    <FormControl className={classes.resultsNum}>
                        <InputLabel htmlFor="resultsNum">Results</InputLabel>
                        <Select
                            id="resultsNum"
                            value={searchParamsRef.current?.limit}
                            onChange={(event: React.ChangeEvent<{ name?: string; value: unknown }>): void => {
                                submitSearch({
                                    ...(searchParamsRef.current as SearchParams),
                                    limit: event.target.value as number
                                })
                            }}
                        >
                            <MenuItem value={10}>10</MenuItem>
                            <MenuItem value={20}>20</MenuItem>
                            <MenuItem value={50}>50</MenuItem>
                            <MenuItem value={100}>100</MenuItem>
                        </Select>
                    </FormControl>
                </div>
            )}

            <TextField
                id="outlined-helperText2"
                label="Metadata Search"
                fullWidth
                autoFocus
                placeholder="..."
                inputRef={searchMetaInputRef}
                className={classes.textField}
                margin="normal"
                variant="outlined"
                defaultValue={searchParamsRef.current?.metaStr || ''}
                // value={}
                onKeyDown={handleKeyPress}
                onChange={(event): void => {
                    searchParamsRef.current = {
                        ...(searchParamsRef.current as SearchParams),
                        metaStr: event.target.value as string
                    }
                }}
            />
            {advancedVals.length > 0 && (
                <div className={classes.advanced}>
                    {advancedVals.map((row, rid) => (
                        <SearchAdvancedRow
                            key={`advRow-${rid}`}
                            row={row}
                            options={allAdvancedOptions.filter((ao) => {
                                return !advancedVals.find((row, idx) => idx !== rid && row[0] === ao.name)
                            })}
                            changeHandler={(newRow: [string, string], forceSubmit?: boolean): void => {
                                const newAdvanced = [...advancedVals]
                                newAdvanced[rid] = newRow
                                setAdvancedVals(newAdvanced)
                                if (forceSubmit) submitSearch(null, newAdvanced)
                            }}
                        />
                    ))}
                    <div>
                        <Button onClick={(): void => submitSearch(null, [...advancedVals, ['', '']])}>+</Button>
                        <Button onClick={(): void => submitSearch(null, advancedVals.slice(0, -1))}>-</Button>
                    </div>
                </div>
            )}
            <div className={classes.actionBar}>
                <Button color="primary" onClick={toggleAdvanced}>
                    {advancedVals.length > 0 ? 'Hide' : 'Show'} Advanced
                </Button>
                <div className={classes.spacer} />
                <Button
                    className={classes.iconButton}
                    color="primary"
                    variant="contained"
                    onClick={(): void => {
                        submitSearch(searchParamsRef.current as SearchParams)
                    }}
                >
                    <SearchIcon /> Search
                </Button>
                <Button
                    className={classes.iconButton}
                    color="secondary"
                    variant="contained"
                    onClick={(): void => {
                        setAdvancedVals([])
                        submitSearch(appDefaults.searchDefaultParams as SearchParams)
                    }}
                >
                    Reset
                </Button>
            </div>
        </Paper>
    )
}

export default SearchForm
