import React from 'react'
import clsx from 'clsx'
import Typography from '@material-ui/core/Typography'
import ListItem from '@material-ui/core/ListItem'
import ListItemAvatar from '@material-ui/core/ListItemAvatar'
import Avatar from '@material-ui/core/Avatar'

import { WarehouseTags } from '@riverscapes/react-common'
import DownloadZipBtn from '../DownloadZipBtn'
// import log from 'loglevel'
import { makeStyles } from '@material-ui/core/styles'
import { Chip } from '@material-ui/core'
import { Project, Program } from '@riverscapes/common'
import { PendingZips } from '../../types'

const useStyles = makeStyles((theme) => ({
    root: {
        padding: theme.spacing(2),
        margin: theme.spacing(2)
    },
    itemText: {
        flexGrow: 1
    },
    spacer: {
        flexGrow: 1
    },
    itemLine: {
        display: 'flex'
    },
    mm: {
        display: 'flex',
        width: '100%'
    },
    odd: {
        background: theme.palette.background.default
    },
    mmItem: {},
    prodChip: {
        height: theme.spacing(2.3),
        borderRadius: theme.spacing(1) / 2,
        color: '#999',
        border: `1px solid #ddd`,
        background: '#eee',
        marginRight: theme.spacing(1)
    },
    downloadText: {
        // position: 'absolute',
        textAlign: 'center',
        width: '100%'
        // bottom: -theme.spacing(2),
        // right: 0
    }
}))

interface Props {
    project: Project
    odd?: boolean
    onClick: () => void
    program: Program
    pendingZips: PendingZips
    downloadZipFile: (proj: Project) => Promise<void>
}

const SearchResult: React.FC<Props> = ({ project, onClick, program, pendingZips, odd, downloadZipFile }: Props) => {
    const classes = useStyles()
    const pendingSince = pendingZips[project.id]

    // Figure out which fields are mandatory
    const projObj = program.products.find((p) => p.id === project.projType)
    const projObjMand = projObj ? projObj.pathArr.filter((pr) => pr.type === 'Collection') : []

    const pTypeName = projObj ? projObj.name : '<UNKNOWN>'
    const mandatoryMeta = projObjMand.map((po) => ({ key: po.name, val: project.meta[po.name] }), {})

    return (
        <ListItem button onClick={onClick} className={clsx({ [classes.odd]: odd as boolean })}>
            <ListItemAvatar>
                <Avatar> {project.projType && project.projType.substr(0, 1)} </Avatar>
            </ListItemAvatar>
            <div className={classes.itemText}>
                <div className={classes.itemLine}>
                    <Typography variant="body1">{project.name}</Typography>
                    <div className={classes.spacer} />
                    <WarehouseTags tags={project.tags} chipProps={{ size: 'small', color: 'secondary' }} />
                </div>
                <div className={classes.mm}>
                    <Chip className={classes.prodChip} label={pTypeName} size="small" />
                    {mandatoryMeta.map(({ key, val }, idx) => {
                        const mmFound = program.collections.find((c) => c.name === key)
                        const color = mmFound && mmFound.color ? mmFound.color[500] : 'inherit'
                        return (
                            <Typography
                                key={`mmval-${idx}`}
                                className={classes.mmItem}
                                variant="caption"
                                color="textSecondary"
                            >
                                {idx > 0 && '  '}
                                <span style={{ color }}> {val} </span>
                            </Typography>
                        )
                    })}
                </div>
            </div>
            <DownloadZipBtn {...{ project, pendingSince, downloadZipFile }} />
        </ListItem>
    )
}

export default SearchResult
