import { useEffect, useRef, useState } from 'react'
import { Match } from 'react-router-dom'
import { Program, Project, getProject } from '@riverscapes/common'
import { endpoints } from '../config'
import { UseHostedReturn } from '@northarrowresearch/react-auth'
import log from 'loglevel'

type UseProjectState = {
    project?: Project
    loading: boolean
}

const useProject = (auth: UseHostedReturn, match?: Match, program?: Program): UseProjectState => {
    const [projState, setProjState] = useState<UseProjectState>({ loading: false })
    const currProjectStateRef = useRef<UseProjectState>()
    currProjectStateRef.current = projState

    useEffect((): void => {
        if (!match || !match.params) return
        if (!match.params.project) {
            setProjState({ loading: false })
        }
        try {
            if (!program || !match || !match.params || !match.params.project || match.params.project.length < 10) return
            const progMachineName = program.machineName
            let done = false
            // Set a timeout so that quick-loads don't give us a status bar
            setTimeout(() => {
                if (!done) setProjState({ ...currProjectStateRef.current, loading: true })
            }, 500)
            auth.getCurrentToken()
                .then((jwt) => getProject(endpoints.graphql, jwt, match.params.project, progMachineName))
                .then((res) => {
                    done = true
                    log.debug('getProjectById::Response', res)
                    if (
                        res.data &&
                        currProjectStateRef.current &&
                        currProjectStateRef.current.project &&
                        currProjectStateRef.current?.project.id === res.data.project.id
                    ) {
                        return
                    } else if (res.data) {
                        setProjState({ project: res.data.project, loading: false })
                    } else {
                        setProjState({ project: undefined, loading: false })
                    }
                })
                .catch((err) => {
                    done = true
                    setProjState({ project: undefined, loading: false })
                    log.error('api.getProjectById error', err)
                })
        } catch {
            return
        }
    }, [match, program, auth])

    return projState
}

export default useProject
