import React, { useState } from 'react'
import TextField from '@material-ui/core/TextField'
import Typography from '@material-ui/core/Typography'
import FormControl from '@material-ui/core/FormControl'
import Select from '@material-ui/core/Select'
import MenuItem from '@material-ui/core/MenuItem'
import { makeStyles } from '@material-ui/core'

import { util } from '@northarrowresearch/common'
import { CollectionType } from '@riverscapes/common'
import log from 'loglevel'

const useStyles = makeStyles((theme) => ({
    root: {
        display: 'flex'
    },
    advancedLeft: {
        flex: '1 1 30%',
        height: theme.spacing(4),
        marginRight: theme.spacing(1)
    },
    advancedLeftCustom: {
        textAlign: 'center'
    },
    advancedLeftOption: {},
    advancedText: {
        flex: '1 1 70%'
    },
    advancedRight: {
        flex: '1 1 70%'
    },
    advancedOp: {
        display: 'flex'
    },
    advancedTextCustomName: {
        flex: '1 1 30%',
        marginRight: theme.spacing(1)
    },
    advancedTextCustomValue: {
        flex: '1 1 40%'
    },
    advancedOpName: {
        flexGrow: 1
    },

    advancedOpNamId: {},
    advancedInput: {
        height: theme.spacing(4)
    }
}))

interface Props {
    row: string[]
    options: CollectionType[]
    changeHandler: (newRow: [string, string], forceSubmit?: boolean) => void
}

const SearchAdvancedRow: React.FC<Props> = ({ row, options, changeHandler }: Props) => {
    const classes = useStyles()
    const [mKey, mVal] = row
    const [err, setErr] = useState<string | null>(null)

    const option = mKey && mKey.length > 0 && options.find((o) => o.name.toLowerCase() === mKey.toLowerCase())
    const hasSetVals = option && option.Allow && !!option.Allow.find((o) => o.name.toLowerCase() && o.name.length > 0)

    const validate = (keyU: unknown, valU: unknown): void => {
        // regex validation
        const key = String(keyU)
        const val = String(valU)
        if (val && val.length > 0 && option && !hasSetVals) {
            const allowRegex = option.AllowRegex
            if (allowRegex && allowRegex.length > 0) {
                const errs: string[] = []
                allowRegex.forEach((regex) => {
                    // log.info(regex)
                    const pattern = new RegExp(regex)
                    const hasError = !val.match(pattern)
                    if (hasError) {
                        errs.push(regex)
                    }
                })
                // Bail on the query if there are any errors. Display them in the error column
                if (errs.length > 0) {
                    if (errs.length === 1) setErr(`Value must match pattern: '${errs[0]}'`)
                    else {
                        setErr(
                            errs.reduce(
                                (acc, err, idx) => acc + (idx > 0 ? ' and ' : '') + err,
                                'Value must match patterns: '
                            )
                        )
                    }
                    return
                }
            }
        }
        setErr(null)
        log.debug('CHANGING VALUES', [key, val])
        if (key.toLowerCase() !== mKey.toLowerCase() || val.toLowerCase() !== mVal.toLowerCase()) {
            changeHandler([key, val])
        }
    }

    const debouncedValidate = util.debounce(validate, 500)

    const handleKeyPress = (keyCode): void => {
        if (keyCode === 13 && mKey && mKey.length > 0) {
            log.debug('Enter pressed')
            changeHandler([mKey, mVal], true)
        }
    }

    return (
        <div className={classes.root}>
            <FormControl className={classes.advancedLeft}>
                <Select
                    value={option ? option.name : '__custom'}
                    inputProps={{
                        className: classes.advancedInput
                    }}
                    onChange={(
                        event: React.ChangeEvent<{
                            name?: string
                            value: unknown
                        }>
                    ): void => changeHandler([String(event.target.value), ''], true)}
                >
                    <MenuItem key={`aoss-custom`} value="__custom">
                        <Typography className={classes.advancedLeftCustom} color="textSecondary">
                            <em>Custom</em>
                        </Typography>
                    </MenuItem>
                    {options.map(({ name }, oid) => (
                        <MenuItem key={`aoss-${oid}`} value={name}>
                            <Typography className={classes.advancedLeftOption}>{name}</Typography>
                        </MenuItem>
                    ))}
                </Select>
            </FormControl>
            {hasSetVals ? (
                <FormControl className={classes.advancedRight}>
                    <Select
                        value={mVal}
                        inputProps={{
                            className: classes.advancedInput
                        }}
                        onChange={(
                            event: React.ChangeEvent<{
                                name?: string
                                value: unknown
                            }>
                        ): void => changeHandler([mKey, String(event.target.value)], true)}
                    >
                        {option &&
                            option.Allow &&
                            option.Allow.map(({ id, name }, oid) => {
                                return (
                                    <MenuItem className={classes.advancedOp} key={`aooptss-${oid}`} value={id}>
                                        <Typography className={classes.advancedOpName}>{name}</Typography>
                                        {name !== id && (
                                            <Typography variant="caption" className={classes.advancedOpNamId}>
                                                {id}
                                            </Typography>
                                        )}
                                    </MenuItem>
                                )
                            })}
                    </Select>
                </FormControl>
            ) : option ? (
                <TextField
                    error={!!err}
                    label={err}
                    disabled={!mKey || mKey.length === 0}
                    placeholder="..."
                    defaultValue={mVal}
                    className={classes.advancedText}
                    inputProps={{
                        className: classes.advancedInput
                    }}
                    onKeyDown={(e: React.KeyboardEvent<HTMLDivElement>): void => handleKeyPress(e.keyCode)}
                    onChange={(e: React.ChangeEvent<HTMLInputElement>): void => debouncedValidate(mKey, e.target.value)}
                />
            ) : (
                <React.Fragment>
                    <TextField
                        placeholder="Meta Name"
                        defaultValue=""
                        className={classes.advancedTextCustomName}
                        inputProps={{
                            className: classes.advancedInput
                        }}
                        onKeyDown={(e: React.KeyboardEvent<HTMLDivElement>): void => handleKeyPress(e.keyCode)}
                        onChange={(e: React.ChangeEvent<HTMLInputElement>): void =>
                            debouncedValidate(e.target.value, mVal)
                        }
                    />
                    <TextField
                        placeholder="Meta Value"
                        defaultValue=""
                        className={classes.advancedTextCustomValue}
                        inputProps={{
                            className: classes.advancedInput
                        }}
                        onKeyDown={(e: React.KeyboardEvent<HTMLDivElement>): void => handleKeyPress(e.keyCode)}
                        onChange={(e: React.ChangeEvent<HTMLInputElement>): void =>
                            debouncedValidate(mKey, e.target.value)
                        }
                    />
                </React.Fragment>
            )}
        </div>
    )
}

export default SearchAdvancedRow
