import React, { useState } from 'react'

import { makeStyles } from '@material-ui/core/styles'
import Link from '@material-ui/core/Link'
import Tooltip from '@material-ui/core/Tooltip'
import Dialog from '@material-ui/core/Dialog'
import DialogTitle from '@material-ui/core/DialogTitle'
import DialogContent from '@material-ui/core/DialogContent'
// import Chip from '@material-ui/core/Chip'
import IconButton from '@material-ui/core/IconButton'
import Divider from '@material-ui/core/Divider'
import CloseIcon from '@material-ui/icons/Close'
import Typography from '@material-ui/core/Typography'
import Popover from '@material-ui/core/Popover'

import InsertDriveFileIcon from '@material-ui/icons/InsertDriveFile'
import PhotoIcon from '@material-ui/icons/Photo'
import Terrain from '@material-ui/icons/Terrain'
import Assignment from '@material-ui/icons/Assignment'

import TreeView from '@material-ui/lab/TreeView'
import ExpandMoreIcon from '@material-ui/icons/ExpandMore'
import ChevronRightIcon from '@material-ui/icons/ChevronRight'

import { WarehouseTags } from '@riverscapes/react-common'
import clsx from 'clsx'
import filesize from 'filesize'
import moment from 'moment'
// import log from 'loglevel'
import style from './ProjectModal.style'
import DownloadZipBtn from '../DownloadZipBtn'
import { PendingZips } from '../../types'
import RecurseTreeItem from './RecurseTreeItem'
import { Project, Program } from '@riverscapes/common'

const useStyles = makeStyles(style)

interface Props {
    onClose: () => void
    project: Project
    downloadFile: (proj: Project, fkey: string) => void
    program: Program
    pendingZips: PendingZips
    downloadZipFile: (proj: Project) => Promise<void>
}

const ProjectModal: React.FC<Props> = ({
    onClose,
    project,
    downloadFile,
    program,
    pendingZips,
    downloadZipFile
}: Props) => {
    const classes = useStyles()
    const [popOver, setPopover] = useState<[any, any] | null>(null)
    const pendingSince = pendingZips[project.id]

    const iconMapping = [
        { pattern: /.*\.(png|gif|jpg|jpeg)$/i, icon: <PhotoIcon className={classes.photoIcon} />, classStr: 'photo' },
        { pattern: /.*\.(tif|img|shp)$/i, icon: <Terrain className={classes.TerrainIcon} />, classStr: 'terrain' },
        {
            pattern: /.*\.(csv|pdf|doc)$/i,
            icon: <Assignment className={classes.documentIcon} />,
            classStr: 'assignment'
        }
    ]

    const files = project.files.reduce((acc, file) => {
        const found = iconMapping.find(({ pattern }) => file.key.match(pattern))
        return {
            ...acc,
            [file.key]: {
                ...file,
                icon: found ? found.icon : <InsertDriveFileIcon className={classes.normalIcon} />,
                classStr: found ? found.classStr : 'normalIcon'
            }
        }
    }, {})
    const fileTree = project.treeIzeFilePaths()
    const pType = program.products.find((p) => p.id === project.projType)
    const pTypeName = pType ? pType.name : 'Unknown'

    const descriptionTag = Object.keys(project.meta).find((k) => k.toLowerCase() === 'description')

    return (
        <Dialog
            onClose={onClose}
            open
            maxWidth="md"
            fullWidth
            className={classes.root}
            classes={{
                scrollPaper: classes.scrollPaper
            }}
        >
            <DialogTitle disableTypography className={classes.titlebar}>
                <div>
                    <Tooltip title={<Typography variant="body1">{project.name}</Typography>}>
                        <Typography variant="h5" component="div" className={clsx(classes.name, classes.allowSelect)}>
                            {project.name}
                        </Typography>
                    </Tooltip>
                    <div className={classes.subHeader}>
                        <div className={classes.flexGrow}>
                            <Typography variant="caption" color="inherit" component="div">
                                ID: <span className={classes.allowSelect}>{project.id}</span>
                            </Typography>
                        </div>
                        <DownloadZipBtn
                            title="Download Project"
                            {...{ project, pendingSince, downloadZipFile }}
                            btnProps={{
                                variant: 'contained',
                                color: 'secondary',
                                size: 'small'
                            }}
                        />
                    </div>
                </div>
                {onClose ? (
                    <IconButton aria-label="close" className={classes.closeButton} onClick={onClose}>
                        <CloseIcon />
                    </IconButton>
                ) : null}
            </DialogTitle>
            <DialogContent dividers>
                <div className={classes.allowSelect}>
                    <Typography variant="caption" component="div" color="inherit">
                        <strong>Project Type</strong>: <strong>{pTypeName}</strong> ({pType && pType.id})
                    </Typography>
                    <Typography variant="caption" component="div" color="inherit">
                        <strong>Created</strong>: {moment(project.createdOn, 'x').fromNow()} (
                        {moment(project.createdOn, 'x').format('lll')})
                    </Typography>
                    <Typography variant="caption" component="div" color="inherit">
                        <strong>Updated</strong>: {moment(project.updatedOn, 'x').fromNow()} (
                        {moment(project.updatedOn, 'x').format('lll')})
                    </Typography>

                    <Typography variant="caption" component="div" color="inherit">
                        <strong>Warehouse tags:</strong>
                        <WarehouseTags tags={project.tags} chipProps={{ size: 'small', color: 'secondary' }} />
                    </Typography>
                    <Typography variant="caption" component="div" color="inherit">
                        <strong>Size (Unzipped)</strong>:{' '}
                        {project.totalSize && project.totalSize > -1 ? filesize(project.totalSize) : 'Not available'}
                    </Typography>
                    {descriptionTag && (
                        <div>
                            <Typography className={classes.title} color="inherit" component="div" variant="subtitle1">
                                Description
                            </Typography>
                            <Typography color="textSecondary" variant="body2">
                                {project.meta[descriptionTag]}
                            </Typography>
                        </div>
                    )}
                    <Typography className={classes.title} color="inherit" variant="subtitle1">
                        Metadata
                    </Typography>
                    <Divider />
                    <div className={classes.metaDisplay}>
                        {Object.keys(project.meta)
                            .filter((k) => ['description'].indexOf(k.toLowerCase()) === -1)
                            .map((metaKey, idk) => {
                                let textComponent: React.ReactNode = project.meta[metaKey]
                                if (project.meta[metaKey].indexOf('http') === 0) {
                                    textComponent = (
                                        <Link href={project.meta[metaKey]} target="_blank">
                                            {project.meta[metaKey]}
                                        </Link>
                                    )
                                }
                                return (
                                    <Tooltip
                                        key={`result-${idk}`}
                                        placement="bottom"
                                        title={
                                            <React.Fragment>
                                                <Typography variant="body1" component="div">
                                                    <strong>{metaKey}</strong>
                                                </Typography>
                                                <Typography variant="body2" component="div">
                                                    {textComponent}
                                                </Typography>
                                            </React.Fragment>
                                        }
                                    >
                                        <div className={classes.metaDisplayItem}>
                                            <Typography variant="caption" component="div" color="textPrimary">
                                                <strong>{metaKey}</strong>
                                            </Typography>

                                            <Typography variant="caption" component="div" color="textPrimary">
                                                {textComponent}
                                            </Typography>
                                        </div>
                                    </Tooltip>
                                )
                            })}
                    </div>
                </div>
                <Typography className={classes.title} color="inherit" variant="subtitle1">
                    Files
                </Typography>
                <Divider />
                <TreeView
                    className={classes.treeRoot}
                    defaultExpanded={['1']}
                    disableSelection
                    defaultCollapseIcon={<ExpandMoreIcon color="inherit" />}
                    defaultExpandIcon={<ChevronRightIcon color="inherit" />}
                >
                    {fileTree.map((ft, idx) => (
                        <RecurseTreeItem
                            project={project}
                            key={`treeNode-${idx}`}
                            keyStr={`treeNode-${idx}`}
                            item={ft}
                            fileDict={files}
                            fileClick={downloadFile}
                        />
                    ))}
                </TreeView>
            </DialogContent>
            <Popover
                open={Boolean(popOver)}
                anchorEl={popOver ? popOver[0] : null}
                onClose={(): void => setPopover(null)}
                anchorOrigin={{
                    vertical: 'bottom',
                    horizontal: 'center'
                }}
                transformOrigin={{
                    vertical: 'top',
                    horizontal: 'center'
                }}
            >
                <Typography className={classes.popOver}>{popOver ? popOver[1] : ''}</Typography>
            </Popover>
        </Dialog>
    )
}
export default ProjectModal
