import React, { useState, useEffect, useRef, useCallback } from 'react'
import { Program, WarehouseRoleEnum, adminGetUsers, adminUpdateUserPermissions } from '@riverscapes/common'
import AdminPanel from './AdminPanel'
import { UserRowType } from './AdminRow'
import { Match, History } from 'react-router-dom'
import { endpoints } from '../../config'
import useProgram from '../../hooks/useProgram'
import { UseHostedReturn } from '@northarrowresearch/react-auth'
import log from 'loglevel'

interface Props {
    programs?: Program[]
    match?: Match
    history?: History
    auth: UseHostedReturn
}
type UserState = {
    users: UserRowType[]
    loading: boolean
}

const AdminConnect: React.FC<Props> = ({ match, programs, auth, history }: Props) => {
    const [userState, setUsersState] = useState<UserState>({
        users: [],
        loading: true
    })
    const userStateRef = useRef<UserState>()
    const nextTokenRef = useRef<string>()
    userStateRef.current = userState

    const program = useProgram(match, programs)

    // Safety check and redirect if not allowed
    useEffect(() => {
        if (
            history &&
            program &&
            program.role !== WarehouseRoleEnum.SuperUser &&
            program.role !== WarehouseRoleEnum.Admin
        ) {
            history.push(`/${program.machineName}`)
        }
    }, [program, history])

    useEffect(() => {
        if (!program || !auth) return
        async function startFunc(): Promise<void> {
            let first = true
            while (first || nextTokenRef.current) {
                first = false
                nextTokenRef.current = await auth
                    .getCurrentToken()
                    .then((jwt) =>
                        adminGetUsers(endpoints.graphql, jwt, program?.machineName as string, 50, nextTokenRef.current)
                    )
                    .then((res) => {
                        const newToken = res.data?.getUsers.nextToken
                        // filter out the self. You're not allowed to change your own permissions
                        const myToken = auth.user ? auth.user.accessToken.payload.username : 'NOBODY'
                        const incomingUsers = (res.data?.getUsers.users || []).filter((u) => u.username !== myToken)

                        setUsersState({
                            users: [...(userStateRef.current?.users || []), ...incomingUsers],
                            loading: Boolean(newToken)
                        })
                        return newToken
                    })
                    .catch((err) => {
                        log.error(err)
                        return undefined
                    })
            }
        }
        startFunc()
    }, [program, auth])

    const userChange = useCallback(
        (userId: string, newRole: WarehouseRoleEnum | null): Promise<void> => {
            // Store the user so we can return it if the query fails
            const foundUser = (userStateRef.current as UserState).users.find((u) => u.username === userId)
            if (!foundUser) throw new Error('Could not find user to change permissions of')
            const backupUser = Object.assign({}, foundUser)

            // Optimistically set the change we want to see with loading greyed out
            setUsersState({
                ...(userStateRef.current as UserState),
                users: (userStateRef.current as UserState).users.map((u) => {
                    if (u.username !== userId) return u
                    else {
                        return {
                            ...u,
                            role: newRole,
                            loading: true
                        }
                    }
                })
            })
            const reset = (): void => {
                log.error('ADMIN:::Operation failed. Putting the record back')
                setUsersState({
                    ...(userStateRef.current as UserState),
                    users: (userStateRef.current as UserState).users.map((u) => {
                        if (u.username !== userId) return u
                        else return backupUser
                    })
                })
            }
            const myTimeout = setTimeout(reset, 3000)
            return auth
                .getCurrentToken()
                .then((jwt) =>
                    adminUpdateUserPermissions(endpoints.graphql, jwt, userId, program?.machineName as string, newRole)
                )
                .then((data) => {
                    clearTimeout(myTimeout)
                    // Set the optimistic loading state first
                    setUsersState({
                        ...(userStateRef.current as UserState),
                        users: (userStateRef.current as UserState).users.map((u) => {
                            if (u.username !== userId) return u
                            else
                                return {
                                    ...u,
                                    ...data.data?.updateUser,
                                    loading: false
                                }
                        })
                    })
                    log.debug('Admin UPDATE', data)
                })
                .catch((err) => {
                    log.error('User update query failure', err)
                    reset()
                })
        },
        [auth, program]
    )

    return (
        <AdminPanel
            users={userState.users}
            isSuperUser={Boolean(program && program.role === WarehouseRoleEnum.SuperUser)}
            onChange={userChange}
            loading={Boolean(userState.loading)}
            program={program as Program}
        />
    )
}
export default AdminConnect
