/* eslint-disable @typescript-eslint/no-use-before-define */
import React, { useState, useRef, useCallback, useMemo } from 'react'
import { History, Match } from 'react-router-dom'
import _ from 'lodash'
import log from 'loglevel'
import NotAllowedModal from '../components/NotAllowedModal'
// import { useHosted } from '@northarrowresearch/react-auth'
import { UseHostedReturn } from '@northarrowresearch/react-auth'
import ProjectModal from '../components/ProjectModal/ProjectModal'
import { Typography, CircularProgress } from '@material-ui/core'

import { Loading } from '@northarrowresearch/react-common'
import ResultsPaneConnect from './SearchResults.connect'
import SearchFormConnect from './SearchForm.connect'
import useProgram from '../hooks/useProgram'
import useProject from '../hooks/useProject'
import { endpoints, appDefaults } from '../config'
import {
    downloadFile,
    Project,
    Program,
    downloadZipFile as apiDownloadZip,
    WarehouseRoleEnum
} from '@riverscapes/common'

import { PendingZips, SearchParams } from '../types'

const url = endpoints.graphql
/**
 * This is the over-arching wrapper for the app. It combines all the
 * same place. Time will tell if that's a good strategy.
 */
interface Props {
    auth: UseHostedReturn
    match: Match
    history: History
    programs: Program[]
}

const AppConnect: React.FC<Props> = ({ match, history, auth, programs }: Props) => {
    // This is the project popup.

    const [pendingZips, setPendingZips] = useState<PendingZips>({})
    const [searchParams, setSearchParams] = useState<SearchParams>(appDefaults.searchDefaultParams) // maybe context?

    // give access to pendingZips asynchronously
    const pendingZipsRef = useRef<PendingZips>()
    pendingZipsRef.current = pendingZips

    const onProgramChange = useCallback((newProg: Program) => {
        setSearchParams(appDefaults.searchDefaultParams)
    }, [])

    const program = useProgram(match, programs, onProgramChange)
    const { project, loading: isProjLoading } = useProject(auth, match, program)

    const navigate: (progName: string, projId?: string) => void = useCallback(
        (progName: string, projId?: string): void => {
            if (projId && projId.length > 10) history.push('/' + progName + '/' + projId)
            else history.push('/' + progName)
        },
        [history]
    )

    const downloadFileFn = useCallback(
        async (project: Project, fkey: string): Promise<void> => {
            if (!program) return Promise.resolve()
            try {
                const jwt = await auth.getCurrentToken()
                const res = await downloadFile(
                    endpoints.graphql,
                    jwt,
                    project.projType,
                    program.machineName,
                    project.id,
                    fkey
                )
                try {
                    if (res.data) window.open(res.data.downloadFile.downloadUrl, '_self')
                } catch (err) {
                    log.error('DOWNLOAD err', err)
                }
            } catch (error) {
                log.error('DOWNLOAD err', error)
            }
        },
        [program, auth]
    )
    const downloadZipFile = useCallback(
        (project: Project): Promise<void> => {
            if (!program) return Promise.resolve()
            const checkZipFunc = (): Promise<void> =>
                auth
                    .getCurrentToken()
                    .then((jwt) =>
                        apiDownloadZip(endpoints.graphql, jwt, project.projType, program.machineName, project.id)
                    )
                    .then((res) => {
                        try {
                            if (res.data && !res.data.downloadZip.pendingSince) {
                                setPendingZips(_.pickBy(pendingZipsRef.current, (val, key) => key !== project.id))
                                window.open(res.data.downloadZip.downloadUrl, '_self')
                            } else {
                                setPendingZips({
                                    ...pendingZipsRef.current,
                                    [project.id]: res.data ? res.data.downloadZip.pendingSince : Date.now()
                                })
                                setTimeout(checkZipFunc, 5000)
                            }
                        } catch (err) {
                            log.error('DOWNLOAD err', err)
                        }
                    })
                    .catch((err) => {
                        log.error('DOWNLOAD err', err)
                    })
            // try once
            return checkZipFunc()
        },
        [program, auth]
    )

    // Modal should be pure and unaffected by anything other than project state
    const projectModal = useMemo(
        () =>
            project && (
                <ProjectModal
                    program={program as Program}
                    project={project}
                    onClose={(): void => {
                        if (program) navigate(program.machineName)
                    }}
                    downloadFile={downloadFileFn}
                    pendingZips={pendingZips}
                    downloadZipFile={downloadZipFile}
                />
            ),
        [project, downloadFileFn, downloadZipFile, navigate, pendingZips, program]
    )

    const hasViewAccess = program && Object.values(WarehouseRoleEnum).indexOf(program.role) > -1
    log.info('App.Connect', { hasViewAccess, program })
    // The searchConenct should only be affected by search state
    const searchConnect = useMemo(
        () => (
            <React.Fragment>
                <SearchFormConnect
                    {...{
                        getCurrentToken: auth.getCurrentToken,
                        url,
                        program: program as Program,
                        searchParams,
                        setSearchParams
                    }}
                />
                <ResultsPaneConnect
                    {...{
                        getCurrentToken: auth.getCurrentToken,
                        url,
                        navigate,
                        program: program as Program,
                        searchParams,
                        setSearchParams,
                        pendingZips,
                        downloadZipFile
                    }}
                />
            </React.Fragment>
        ),
        [searchParams, program, pendingZips, downloadZipFile, auth.getCurrentToken, navigate]
    )

    if (program && !hasViewAccess)
        return <NotAllowedModal program={program as Program} onClose={(): void => history.push('/')} />

    return (
        <React.Fragment>
            {isProjLoading && (
                <Loading
                    logoSVG={<CircularProgress size="7rem" thickness={7} />}
                    status={
                        <Typography variant="h5" component="div">
                            Loading Project...
                        </Typography>
                    }
                    containerProps={{
                        maxWidth: 'xl',
                        style: {
                            position: 'absolute',
                            zIndex: 1000,
                            top: 0,
                            left: 0,
                            width: '100%',
                            height: '100%',
                            background: '#FFFFFF77'
                        }
                    }}
                />
            )}
            {searchConnect}
            {projectModal}
        </React.Fragment>
    )
}
export default AppConnect
