import React, { useState, useMemo } from 'react'
import Toolbar from '@material-ui/core/Toolbar'
import { WarehouseRoleEnum, UserType } from '@riverscapes/common'
import AdminRow from './AdminRow'
import List from '@material-ui/core/List'
import FormControl from '@material-ui/core/FormControl'
import Paper from '@material-ui/core/Paper'
import InputAdornment from '@material-ui/core/InputAdornment'
import Input from '@material-ui/core/Input'
import { makeStyles } from '@material-ui/core/styles'
import SearchOutlinedIcon from '@material-ui/icons/SearchOutlined'
import { Typography } from '@material-ui/core'

const useStyles = makeStyles((theme) => ({
    root: {
        display: 'flex',
        overflow: 'hidden',
        flexDirection: 'column',
        margin: theme.spacing(2)
    },
    description: {
        margin: theme.spacing(0, 2)
    },
    list: {
        flexGrow: 1,
        overflow: 'scroll',
        margin: theme.spacing(1, 2)
    },
    formControl: {
        margin: theme.spacing(1, 2)
    }
}))

interface Props {
    users: UserType[]
    isSuperUser?: boolean
    title: string
    description?: string
    onChange: (userId: string, newRole: WarehouseRoleEnum | null) => void
}

const AdminList: React.FC<Props> = ({ users, onChange, title, description, isSuperUser }: Props) => {
    const classes = useStyles()
    const [search, setSearch] = useState<string>()
    const dispUsers = useMemo(() => {
        if (search && search.length >= 3) {
            const searchArr = (search as string).split(' ').filter((s) => s.length >= 3)
            return users.filter((u) => {
                // Make sure every work is represented in the name or email
                return searchArr.every((word) => {
                    return (
                        u.name.toLocaleLowerCase().indexOf(word.toLocaleLowerCase()) > -1 ||
                        u.email.toLocaleLowerCase().indexOf(word.toLocaleLowerCase()) > -1
                    )
                })
            })
        } else return users
    }, [users, search])

    return (
        <Paper className={classes.root}>
            <Toolbar>
                <Typography variant="subtitle1">{title}</Typography>
            </Toolbar>
            <Typography className={classes.description} variant="caption">
                {description || ' '}
            </Typography>
            <FormControl className={classes.formControl}>
                <Input
                    fullWidth
                    onChange={(e): void => setSearch(e.target.value)}
                    startAdornment={
                        <InputAdornment position="start">
                            <SearchOutlinedIcon />
                        </InputAdornment>
                    }
                />
            </FormControl>
            <List className={classes.list}>
                {dispUsers.map((u, idx) => (
                    <AdminRow key={`userRow-${idx}`} user={u} onChange={onChange} isSuperUser={isSuperUser} />
                ))}
            </List>
        </Paper>
    )
}
export default AdminList
