import React, { useState, useEffect } from 'react'

import { search, SearchResultsInterface, Program, Project } from '@riverscapes/common'
import { SearchParams, PendingZips } from '../types'

import SearchResults from '../components/Search/SearchResults'
import log from 'loglevel'

interface Props {
    program?: Program
    getCurrentToken: () => Promise<string>
    url: string
    searchParams: SearchParams
    setSearchParams: (params: SearchParams) => void
    navigate: (progName: string, projId?: string) => void
    pendingZips: PendingZips
    downloadZipFile: (proj: Project) => Promise<void>
}

const ResultsPaneConnect: React.FC<Props> = ({
    getCurrentToken,
    url,
    navigate,
    program,
    searchParams,
    setSearchParams,
    pendingZips,
    downloadZipFile
}: Props) => {
    const [isLoading, setIsLoading] = useState<boolean>(true)
    const [results, setResults] = useState<SearchResultsInterface>()
    const [errors, setErrors] = useState<any>()
    if (errors) log.error(errors)

    useEffect(() => {
        setResults(undefined)
        async function doEffect(): Promise<void> {
            if (!program) return
            setIsLoading(true)
            const jwt = await getCurrentToken()
            log.debug('Search API Hit', { url, jwt, program: program.machineName, searchParams })
            await search(url, jwt, program.machineName, searchParams)
                .then(({ data, errors }) => {
                    try {
                        if (errors && errors.length > 0) {
                            setErrors(errors)
                            setIsLoading(false)
                        } else if (data) {
                            const searchData = data.search
                            setResults(searchData)
                            setIsLoading(false)
                        }
                    } catch {
                        setIsLoading(false)
                    }
                })
                .catch((err) => {
                    setIsLoading(false)
                    setErrors([err])
                })
        }
        doEffect()
    }, [program, url, searchParams, getCurrentToken])

    return (
        <SearchResults
            data={results}
            loading={isLoading}
            navigate={navigate}
            program={program as Program}
            searchParams={searchParams}
            setSearchParams={setSearchParams}
            pendingZips={pendingZips}
            downloadZipFile={downloadZipFile}
        />
    )
}
ResultsPaneConnect['whyDidYouRender'] = true
export default ResultsPaneConnect
