import { red, green, blue, orange, purple, cyan, lime, brown } from '@material-ui/core/colors'
import log from 'loglevel'
import { SearchParams } from './types'

let domain = process.env.REACT_APP_DOMAIN
if (process.env.NODE_ENV && process.env.NODE_ENV === 'development') {
    domain = 'localhost'
    // basename = ''
}
log.debug('config: ', { domain })
export const isDev = process.env.NODE_ENV && process.env.NODE_ENV === 'development'
export const version = process.env.REACT_APP_VERSION

export const endpoints = {
    graphql: `${process.env.REACT_APP_GRAPHQL}`
}

const searchDefaultParams: SearchParams = {
    limit: 10,
    nextToken: 0
}

export const warehouseIconUrl = process.env.PUBLIC_URL + '/RiverscapesLogo.png'

export const appDefaults = {
    searchDefaultParams
}

export const mandatoryMetaColors = [red, green, blue, orange, purple, cyan, lime, brown]
export const redirectUrl = new URL(
    process.env.PUBLIC_URL.endsWith('/') ? process.env.PUBLIC_URL : process.env.PUBLIC_URL + '/',
    window.location.origin
)

console.log('RedirectUrl', { redirectUrl, publicUrl: process.env.PUBLIC_URL })
