import React from 'react'

import { makeStyles } from '@material-ui/core/styles'
import Dialog from '@material-ui/core/Dialog'
import DialogTitle from '@material-ui/core/DialogTitle'
import DialogContent from '@material-ui/core/DialogContent'
import DialogActions from '@material-ui/core/DialogActions'
import Button from '@material-ui/core/Button'
import ErrorIcon from '@material-ui/icons/Error'
import Typography from '@material-ui/core/Typography'

import { Program } from '@riverscapes/common'

const useStyles = makeStyles((theme) => ({
    root: {
        marginLeft: theme.spacing(1)
    },
    titlebar: {
        display: 'flex',
        background: theme.palette.error.main,
        color: theme.palette.error.contrastText
    },
    closeButton: {},
    name: {
        paddingLeft: theme.spacing(1),
        flexGrow: 1
    }
}))

interface Props {
    onClose: () => void
    program: Program
}

const ProjectModal: React.FC<Props> = ({ onClose, program }: Props) => {
    const classes = useStyles()
    return (
        <Dialog
            onClose={onClose}
            open
            maxWidth="xs"
            fullWidth
            className={classes.root}
            classes={
                {
                    // scrollPaper: classes.scrollPaper
                }
            }
        >
            <DialogTitle disableTypography className={classes.titlebar}>
                <ErrorIcon color="inherit" />
                <Typography className={classes.name}>Access Denied</Typography>
            </DialogTitle>
            <DialogContent dividers>
                <Typography variant="body1" component="div" color="inherit">
                    You don't have access to the program <strong>{program.name}</strong>. If you arrived here via URL
                    you will be able to use the URL again once you have access.
                </Typography>
            </DialogContent>
            <DialogActions>
                <Button onClick={onClose} color="primary">
                    Back to programs
                </Button>
            </DialogActions>
        </Dialog>
    )
}
export default ProjectModal
